.btn {
    width: 25%;
    height: 100%;
    font-size: 3rem;
    font-family: inherit;
    background-color: var(--color-white);
    outline: none;
    border: solid 0.2px transparent;

    display: flex;
    justify-content: center;
    align-items: center;

    transition: all .2s;
}
