.btn:hover {
    color: var(--color-light-orange-transparent);
    background-color: var(--color-light-black);
}

.btn:active {
    transform: scale(.95);
}

.btn--large {
    width: 50%;
}

.btn--operator, .btn--primary {
    color: var(--color-light-white);
    background-color: var(--color-medium-black);
}

.btn--operator:hover, .btn--primary:hover{
    color: var(--color-dark-blue-transparent);
    background-color: var(--color-light-white);
}
